
.comment-content {
  width: 100%;
  margin-left:auto;
  margin-right:auto;
  background: red;
}
.beauty-title {
  text-align: center;
  padding-top: 25px;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
}

.beauty-score-content {
  margin-top: 20px;
  text-align: center;
}

.beauty-score-item {
  position: relative;
  margin-top: 16px;
}
.beauty-score-title{
  position: absolute;
  text-align: right;
  top: 5px;
  padding-right: 10px;
  height: 100%;
  width: 15%;
  font-size: 16px;
  font-weight: 500;
  white-space: nowrap;
}



.beauty-score {
  margin-left: auto;
  margin-right: auto;
  width: 70%;
}
.split-line {
  margin-top: 23px;
  height: 1px;
  background: #ffe6e6e6;
}
.sharpness-content {
  position: relative;
}
.sharpness-title {
  text-align: center;
  margin-top: 26px;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;

}
.feedback-input {
  margin-top: 23px;
  background: #fff5f5f5;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  border: none;
}
.feedback-input:focus {
  background: #fff5f5f5;
}

.commit-btn {
  width: 100%;
  height: 40px;
  margin-left:auto;
  margin-right:auto;
  margin-top: 15px;
  background: #05d380;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  border: none;
}
.commit-btn:hover {
  background: #05d380;
}
.commit-btn:focus {
  background: #05d380;
}



